.header-color{
  background: #DAE2F8;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, black, black);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, black, black); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid{
  background: #DAE2F8;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #D6A4A4, #DAE2F8);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #D6A4A4, #DAE2F8); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  top:0;
  left:0;
  width: 100%;
  height: 100%
}

.grid-text{
  background-color: black;
  opacity: .8;
  width: 80%;
  margin: auto;
  border-radius: 10px;
}

.avatar-img{
  height: 20%;
  margin-top: 5em;
  border-radius: 50%;
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
}

.grid-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin:auto;
}

.social-links i {
  color:white;
  font-size: 5em;
  padding: 8px;
}


.resume-right-col{
  background: #27221F;
  color: white;
  padding: 2em;
}

.projects-grid{
  display: flex;
}

.contact-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid{
  text-align: center;
  width: 80%;
  height: 500px;
  background: white;
  justify-content: center;
  color:black;
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: 'Anton', sans-serif
}

.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width:50%;
  margin: auto;
}

.contact-list i{
  font-size: 50px;
  padding-right: 5rem;
}

.research h1{
  text-align: center;
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.research p{
  padding-bottom: 1em;
  font-size: 1.2em;
}

.research-pics{
  width: 100%;
  height:100%;
}
.about-pics{
  width: 100%;
}

.research hr{
  border-top: 5px dotted black;
  width:80%;
  margin: auto;
  
}

.research-videos{
    justify-content: center;
    align-items: center;
    margin: auto;
    width: 400px;
    display: flex;
    margin-bottom: 10px;
}

.research-pics2{
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 66%;
  display: flex;
}

.resume-left-col{
  justify-content: center;
  align-items: center;
  margin: auto;
  text-align: center;
  top:0;
}